@import '../common/variables.scss';

.main-section__button {
  font-size: 18px;
  font-weight: bold;
  color: white;
  border: 3px solid #fff;
  border-radius: 30px;
  background-color: transparent;
  text-decoration: none;
  padding: 15px 33px;
  margin-top: 40px;
  width: 100%;

  &:hover {
    color: #d22856;
    background-color:#ffffff;
  }

  &:focus {
    color: #f1becc;
    background-color:#ffffff;
  }
}

.main-section__app {
  width: 87%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.main-section__title {
  display: none;
}

.main-section__gadgets {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 10% 0;
  width: 100%;
}

.main-section__gadget {
  width: 60px;
  height: 60px;
}

.main-section__gadget--apple {
  background-image: url(./../images/apple-logopng.png);
  background-position: top left;
  background-repeat: no-repeat;
  background-size: 47px 56px;
}

.main-section__gadget--android {
  background-image: url(./../images/android-logo.png);
  background-position: center 8px;
  background-repeat: no-repeat;
  background-size: 43px 47px;
}

.main-section__gadget--microsoft {
  background-image: url(./../images/microsoft-logo.png);
  background-position: right 12px;
  background-repeat: no-repeat;
  background-size: 44px 41px;
}

.main-section__text {
  font-size: 14px;
  color: #fff;
  line-height: 1.714;
  margin-top: 45px;
}

.main-section__text {
  display: flex;
  align-items: center;
  justify-content: center;
}

.main-section__wrapper {
  padding-bottom: 10%;
}

.main-section__photo {
  display: none;
}

.main-section__brands {
  width: 100%;
}

@media (min-width: $tablet-width) {
  .main-section__title {
    display: flex;
    font-size: 40px;
    color: #fff;
    font-weight: normal;
    line-height: 1.2;
    text-align: center;
    width: 75%;
  }

  .main-section__wrapper {
    width: 45.5%;
    padding-bottom: 4%;
    position: absolute;
    top: 23%;
    left: 3%;
  }

  .main-section__gadgets {
    margin-top: 45px;
    justify-content: space-around;
    width: 80%;
    margin-left: 10%;
  }

  .main-section__app {
    width: 100%;
  }
}

@media (min-width: $desktop-width) {

  .main-section__wrapper {
    width: 25%;
    left: 10%;
    top: 27%;
  }

  .main-section__brands {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .main-section__gadgets {
    width: 100%;
    margin-left: 0;
    margin-top: 65px;
    margin-bottom: 0;
  }

  .main-section__button {
    margin-top: 60px;
  }

  .main-section__text {
    font-size: 18px;
    line-height: 1.333;
    width: 103%;
  }
}